.container {
  display: grid;
  grid-template-columns: auto 120px auto;
  grid-template-rows: auto;
}
.left,
.right,
.middle {
  display: inline-block;
  text-align: center;
}

.left select,
.right select,
.middle button {
  width: 100%;
  margin-top: 12px;
  margin-bottom: 12px;
  padding: 3px 6px;
}
